import React from 'react';
import { RcIconButton, RcCircularProgress } from '@ringcentral/juno';
import { styled } from '@ringcentral/juno/foundation';
import { ViewLogBorder } from '@ringcentral/juno-icon';

type LogButtonProps = {
  className?: string;
  onLog?: (...args: any[]) => any;
  isLogged?: boolean;
  disableLinks?: boolean;
  isLogging?: boolean;
  addTitle?: string;
  editTitle?: string;
};

const StyledWrapper = styled.div`
  position: relative;
  width: 16px;
`;

const StyledSpinner = styled(RcCircularProgress)`
  margin-right: 0;
  position: absolute;
  right: -4px;
  bottom: 6px;
`;

const LogButton: React.SFC<LogButtonProps> = ({
  className,
  onLog,
  isLogged,
  disableLinks,
  isLogging,
}) => {
  if (!isLogged) {
    return null;
  }
  const spinner = isLogging ? (
    <StyledSpinner size={11} />
  ) : null;
  return (
    <StyledWrapper className={className}>
      <RcIconButton
        symbol={ViewLogBorder}
        disabled={disableLinks || isLogging}
        title="View log details"
        onClick={onLog}
        color="action.primary"
        variant="plain"
        size="small"
      />
      {spinner}
    </StyledWrapper>
  );
};

LogButton.defaultProps = {
  className: undefined,
  onLog: undefined,
  isLogged: false,
  disableLinks: false,
  isLogging: false,
};
export default LogButton;
